<template>
  <div class="sole">
     <div class="title-content clearfix">
            <div class="title-box fl">
                <div class="t-text">独家>>></div>
            </div>
            <div class="line-box fl"></div>
            <div class="line-box fl"></div>
            <div class="ad-img-box fr">
                <a target="_blank" href="https://g.163.com/c?AID=259321&amp;FlightID=2057&amp;Values=339870558&amp;Redirect=http://data.163.com/special/datablog/">
                    <img class="ad-img auto-img" src="https://yt-adp.ws.126.net/channel4/1200125_roku_20190313.jpg" border="0"  alt="">
                </a>
            </div>
    </div>

    <div class="banner-content">
        <div class="banner-box">
            <el-carousel :interval="4000" type="card" height="260px" >
                <el-carousel-item v-for="(item,index) in soleNewsData.slice(0,8)" :key="index" >
                    <img class="auto-img" :src="item.images" @click="goDetail(item.id)">
                <!-- <h3 class="medium" >{{ item }}</h3> -->
                </el-carousel-item>
            </el-carousel>
        </div>
    </div>


       


    <div class="newList-content">
        <div class="clearfix">
            <div class="new-list-left-box fl">
                <div class="list-item clearfix" v-for="(item,index) in soleNewsData" :key="index"  @click="goDetail(item.id)">
                    <div class="left-img fl">
                        <el-image class=" err-imgs">
                            <div slot="error" class="image-slot">
                                <i class="el-icon-picture-outline"></i>
                            </div>
                        </el-image>
                        <img class="a-img auto-img" :src="item.images" alt="">
                    </div>
                    <div class="right-cont fl">
                        <div class="title">{{item.title}}</div>
                        <div class="text-cont">
                            <span class="post-name">{{item.posterNickName}}</span>
                            <span class="pub-time">日期：{{item.publishTime}}</span>
                            <span class="comment-count">评论：{{item.commentCount}}</span>
                        </div>
                    </div>
                </div>
                <div class="pagination-page">
                    <el-pagination 
                    class="pag-box" 
                    background 
                    layout="prev, pager, next" 
                    :size-change="10"
                    :total="1000"
                    :current-change = "currentPage"
                    @current-change="handleCurrentChange"
                    >
                    </el-pagination>
                </div>
            </div>

            <div class="new-other-right-box fl">
                <div class="other-list">
                    <div class="public-benefit-advertise">
                        <div class="pub-ad-title">公益·广告栏</div>
                    </div>
                     <div class="advertise-three">
                       <img class="imgs" src="https://ss0.bdstatic.com/70cFuHSh_Q1YnxGkpoWK1HF6hhy/it/u=297240391,1517228721&fm=11&gp=0.jpg" alt="">
                        <div class="title">[公益之家]——人与自然和平相处</div>
                        <div class="texts">拒绝野味，保护野生动物，爱护生命，从我做起！</div>
                    </div>
                    <div class="advertise-one">
                        <div class="ad-box">   
                            <img class="ad-imgs auto-img" src="https://yt-adp.ws.126.net/myzhang/300250_ajdt_20200807.jpg" alt="">
                            <div class="ad-texts">广告</div>
                        </div>
                        <div class="ad-title">宝马·全新BMW 8系四门轿跑车</div> 
                    </div>
                    
                    <div class="advertise-one">
                        <div class="ad-box">
                          <img class="ad-imgs auto-img" src="https://yt-adp.ws.126.net/myzhang/300250_arob_20200714.gif" alt="">
                          <div class="ad-texts">广告</div>
                        </div>
                    </div>

                    <div class="advertise-two">
                        <el-carousel height="190px">
                            <el-carousel-item v-for="item in adbannerData" :key="item">
                                <img class="auto-img" :src="item" alt="">
                            </el-carousel-item>
                        </el-carousel>
                    </div>
                   

                </div>
            </div>
        </div>
    </div>
  </div>
</template>

<script>
import "../../assets/css/sole.less";
export default {
    name:'sole',
  data() {
    return {
      soleNewsData: [],
    
      currentPage:1,
      // 定义页数
       pageNum:'',
       adbannerData:[
           'https://ss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=2042524522,578254534&fm=26&gp=0.jpg',
           'https://ss1.bdstatic.com/70cFuXSh_Q1YnxGkpoWK1HF6hhy/it/u=2209771939,3243558680&fm=26&gp=0.jpg',
           'https://ss3.bdstatic.com/70cFv8Sh_Q1YnxGkpoWK1HF6hhy/it/u=3986918335,685594578&fm=26&gp=0.jpg'
       ],
    };
  },
  created(){
    this.getSoleNewsData();
    
  },
  methods: {
    getSoleNewsData(pageNum) {
        // console.log('pageNum-->',pageNum)
      this.axios({
        method: "GET",
        url: "/list",
        params: {
          apikey: this.appkey,
          category: 'T1370583240249',
          page: pageNum,
        },
      })
        .then((result) => {
            //  console.log('result',result);
             this.soleNewsData = result.data.data;
        
        })
        .catch((err) => {
            //  console.log('err',err);
        });
    },

    //点击页码，获取对应的val值，在传给要显示的页数内容
    handleCurrentChange(val){
        
        // console.log('val',val)
        let pageNum = val;
        // console.log('pageNum',pageNum)
        //调用请求数据，才能获取值
        this.getSoleNewsData(pageNum)
    
    },

    //点击商品进入菜单详情页，
    goDetail(id) {
      // console.log(id);
      this.$router.push({ name: "Detail", query: { id } });
    },
  },
};
</script>

